//
// user.scss
// Use this to write your custom SCSS
//

.navbar-sticky.navbar-stuck {
  z-index: 100;
  top: 0.2rem;
}

.mt-md-n14 {
  margin-top: -12rem !important;
}

.mt-n8 {
  margin-top: 3rem !important;
}

.btn-brand-secondary {
  color: $white;
  background: $brand-secondary;

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.show {
    background-color: darken($brand-secondary, 7.5%);
    border-color: darken($brand-secondary, 7.5%);
    color: $white;
  }

  &:disabled,
  &.disabled {
    color: $btn-dark-color;
  }
}

.btn-outline-brand-secondary {
  border-color: $brand-secondary;
  color: $brand-secondary;

  &:hover, &:focus, &:active,
  &.active, &.show {
    border-color: $brand-secondary;
    background-color: $brand-secondary;
    color: $white !important;
  }
}

.sidebar-enabled::before {
  background: #fff !important;
}

.or {
  display:flex;
  justify-content:center;
  align-items: center;
  color:grey;
}

.or:after,
.or:before {
    content: "";
    display: block;
    background: grey;
    width: 30%;
    height:1px;
    margin: 0 10px;
}
